.u {
    @mixin spacingEach($ptn, $vector, $type: 'm', $device: 'pc') {
        $propName: 'margin';
        @if $type != 'm' {
            $propName: 'padding'; }

        $deviceType: '';
        @if $device == 'tablet' {
            $deviceType: '--tablet'; }
        @if $device == 'sp' {
            $deviceType: '--sp'; }

        @each $qt in $ptn {
            &--#{$qt}#{$deviceType} {
                @if $vector == 't' or $vector == 'y' {
                    #{$propName}-top: $qt + px; }
                @if $vector == 'b' or $vector == 'y' {
                    #{$propName}-bottom: $qt + px; }
                @if $vector == 'l' or $vector == 'x' {
                    #{$propName}-left: $qt + px; }
                @if $vector == 'r' or $vector == 'x' {
                    #{$propName}-right: $qt + px; } } } }


    &-mg {
        &t {
            @include spacingEach($margin-patterns, 't'); }
        &b {
            @include spacingEach($margin-patterns, 'b'); }
        &y {
            @include spacingEach($margin-patterns, 'y'); }
        &l {
            @include spacingEach($margin-patterns, 'l'); }
        &r {
            @include spacingEach($margin-patterns, 'r'); }
        &x {
            @include spacingEach($margin-patterns, 'x'); } }

    &-pd {
        &t {
            @include spacingEach($margin-patterns, 't', 'padding'); }
        &b {
            @include spacingEach($margin-patterns, 'b', 'padding'); }
        &y {
            @include spacingEach($margin-patterns, 'y', 'padding'); }
        &l {
            @include spacingEach($margin-patterns, 'l', 'padding'); }
        &r {
            @include spacingEach($margin-patterns, 'r', 'padding'); }
        &x {
            @include spacingEach($margin-patterns, 'x', 'padding'); } }

    @include tablet {
        &-mg {
            &t {
                @include spacingEach($margin-patterns, 't', 'm', 'tablet'); }
            &b {
                @include spacingEach($margin-patterns, 'b', 'm', 'tablet'); }
            &y {
                @include spacingEach($margin-patterns, 'y', 'm', 'tablet'); }
            &l {
                @include spacingEach($margin-patterns, 'l', 'm', 'tablet'); }
            &r {
                @include spacingEach($margin-patterns, 'r', 'm', 'tablet'); }
            &x {
                @include spacingEach($margin-patterns, 'x', 'm', 'tablet'); } }

        &-pd {
            &t {
                @include spacingEach($margin-patterns, 't', 'padding', 'tablet'); }
            &b {
                @include spacingEach($margin-patterns, 'b', 'padding', 'tablet'); }
            &y {
                @include spacingEach($margin-patterns, 'y', 'padding', 'tablet'); }
            &l {
                @include spacingEach($margin-patterns, 'l', 'padding', 'tablet'); }
            &r {
                @include spacingEach($margin-patterns, 'r', 'padding', 'tablet'); }
            &x {
                @include spacingEach($margin-patterns, 'x', 'padding', 'tablet'); } } }


    @include smartphone {
        &-mg {
            &t {
                @include spacingEach($margin-patterns, 't', 'm', 'sp'); }
            &b {
                @include spacingEach($margin-patterns, 'b', 'm', 'sp'); }
            &y {
                @include spacingEach($margin-patterns, 'y', 'm', 'sp'); }
            &l {
                @include spacingEach($margin-patterns, 'l', 'm', 'sp'); }
            &r {
                @include spacingEach($margin-patterns, 'r', 'm', 'sp'); }
            &x {
                @include spacingEach($margin-patterns, 'x', 'm', 'sp'); } }

        &-pd {
            &t {
                @include spacingEach($margin-patterns, 't', 'padding', 'sp'); }
            &b {
                @include spacingEach($margin-patterns, 'b', 'padding', 'sp'); }
            &y {
                @include spacingEach($margin-patterns, 'y', 'padding', 'sp'); }
            &l {
                @include spacingEach($margin-patterns, 'l', 'padding', 'sp'); }
            &r {
                @include spacingEach($margin-patterns, 'r', 'padding', 'sp'); }
            &x {
                @include spacingEach($margin-patterns, 'x', 'padding', 'sp'); } } } }
